@import '../Helpers/Helpers.scss';

.questions {
    // background-color: $purple-dark;
    // margin: 0;
    // padding: 10px 10px 0 10px;

    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    &__list {
        margin: 0;
        width: 85%;
        padding: 5px 5px 0 5px;
        // width: max-content;
        border-radius: 10px;
        overflow: auto;


        max-height: 37vh;

        @include medium() {
            max-height: 37.8vh;
        }
    
        @include large() {
            max-height: 38vh;
        }

        @include medium() {
            width: 90%;
        }

        >p {
            margin: 0;
            font-size: 8px;
            border-bottom: 1px solid $purple-lite;
            padding: 5px;
    
            @include medium() {
                font-size: 10px;
            }
    
            @include large() {
                font-size: 12px;
            }
        }
    }
}